'use client';

import React, { useState, useEffect } from 'react';
import { Trash2, Plus, Menu, ArrowLeft } from 'lucide-react';

// Types
interface Supplier {
    id: number;
    name: string;
    address: string;
    gstIn: string;
}

interface Medicine {
    id: number;
    name: string;
    purchaseRate: number;
    sellingRate: number;
    gstPercent: number;
    hsnCode: string;
}

interface PurchaseItem {
    id: number;
    itemCode: string;
    medicineId: number;
    medicineName: string;
    batchNo: string;
    expiryDate: string;
    qty: string;
    freeQty: string;
    purchaseRate: string;
    sellingRate: string;
    gstPercent: string;
    amount: number;
    hsnCode: string;
}

interface PurchaseBill {
    purchaseId: number;
    date: string;
    supplierId: number;
    supplierName: string;
    supplierAddress: string;
    gstIn: string;
    invoiceNo: string;
    invoiceDate: string;
    paymentType: string;
    items: PurchaseItem[];
    grossTotal: number;
    gstTotal: number;
    roundOff: number;
    discount: number;
    cgst: number;
    sgst: number;
    igst: number;
    cessAmount: number;
    finalTotal: number;
}

const PurchaseBillPage: React.FC = () => {
    const [sidebarOpen, setSidebarOpen] = useState(false);
    const [purchaseBill, setPurchaseBill] = useState<PurchaseBill>({
        purchaseId: 5314,
        date: new Date().toISOString().split('T')[0],
        supplierId: 0,
        supplierName: '',
        supplierAddress: '',
        gstIn: '',
        invoiceNo: '',
        invoiceDate: new Date().toISOString().split('T')[0],
        paymentType: 'Credit',
        items: [],
        grossTotal: 0,
        gstTotal: 0,
        roundOff: 0,
        discount: 0,
        cgst: 0,
        sgst: 0,
        igst: 0,
        cessAmount: 0,
        finalTotal: 0
    });

    const [suppliers, setSuppliers] = useState<Supplier[]>([]);
    const [medicines, setMedicines] = useState<Medicine[]>([]);
    const [medicineSearches, setMedicineSearches] = useState<{ [key: number]: string }>({});
    const [showMedicineDropdown, setShowMedicineDropdown] = useState<{ [key: number]: boolean }>({});
    const [loading, setLoading] = useState(false);

    // Fetch suppliers from API
    useEffect(() => {
        const fetchSuppliers = async () => {
            try {
                const res = await fetch('https://localhost:7112/api/Suppliers');
                if (res.ok) {
                    const data = await res.json();
                    setSuppliers(data);
                }
            } catch (error) {
                console.error('Error fetching suppliers:', error);
            }
        };
        fetchSuppliers();
    }, []);

    // Fetch medicines from API
    useEffect(() => {
        const fetchMedicines = async () => {
            try {
                const res = await fetch('https://localhost:7112/api/Medicines');
                if (res.ok) {
                    const data = await res.json();
                    setMedicines(data);
                }
            } catch (error) {
                console.error('Error fetching medicines:', error);
            }
        };
        fetchMedicines();
    }, []);

    // Fetch supplier details
    const fetchSupplierDetails = async () => {
        if (!purchaseBill.supplierId) return;

        setLoading(true);
        try {
            const selectedSupplier = suppliers.find(s => s.id === purchaseBill.supplierId);
            if (selectedSupplier) {
                setPurchaseBill(prev => ({
                    ...prev,
                    supplierName: selectedSupplier.name,
                    supplierAddress: selectedSupplier.address,
                    gstIn: selectedSupplier.gstIn
                }));
            }
        } catch (error) {
            console.error('Error fetching supplier details:', error);
        } finally {
            setLoading(false);
        }
    };

    // Add new item
    const addItem = () => {
        const newItem: PurchaseItem = {
            id: Date.now(),
            itemCode: '',
            medicineId: 0,
            medicineName: '',
            batchNo: '',
            expiryDate: '',
            qty: '',
            freeQty: '',
            purchaseRate: '',
            sellingRate: '',
            gstPercent: '',
            amount: 0,
            hsnCode: ''
        };

        setPurchaseBill(prev => ({
            ...prev,
            items: [...prev.items, newItem]
        }));
    };

    // Remove item
    const removeItem = (itemId: number) => {
        setPurchaseBill(prev => ({
            ...prev,
            items: prev.items.filter(item => item.id !== itemId)
        }));
        // Clean up search states
        setMedicineSearches(prev => {
            const newState = { ...prev };
            delete newState[itemId];
            return newState;
        });
        setShowMedicineDropdown(prev => {
            const newState = { ...prev };
            delete newState[itemId];
            return newState;
        });
    };

    // Update item
    const updateItem = (itemId: number, field: keyof PurchaseItem, value: any) => {
        setPurchaseBill(prev => ({
            ...prev,
            items: prev.items.map(item => {
                if (item.id === itemId) {
                    const updatedItem = { ...item, [field]: value };

                    // Calculate amount when qty, purchaseRate, or gstPercent changes
                    if (field === 'qty' || field === 'purchaseRate' || field === 'gstPercent') {
                        const qty = parseFloat(updatedItem.qty) || 0;
                        const purchaseRate = parseFloat(updatedItem.purchaseRate) || 0;
                        const gstPercent = parseFloat(updatedItem.gstPercent) || 0;

                        const baseAmount = qty * purchaseRate;
                        const gstAmount = (baseAmount * gstPercent) / 100;
                        updatedItem.amount = baseAmount + gstAmount;
                    }

                    return updatedItem;
                }
                return item;
            })
        }));
    };

    // Handle medicine search
    const handleMedicineSearch = (itemId: number, searchTerm: string) => {
        setMedicineSearches(prev => ({ ...prev, [itemId]: searchTerm }));
        setShowMedicineDropdown(prev => ({ ...prev, [itemId]: true }));
    };

    // Select medicine
    const selectMedicine = (itemId: number, medicine: Medicine) => {
        updateItem(itemId, 'medicineId', medicine.id);
        updateItem(itemId, 'medicineName', medicine.name);
        updateItem(itemId, 'purchaseRate', medicine.purchaseRate.toString());
        updateItem(itemId, 'sellingRate', medicine.sellingRate.toString());
        updateItem(itemId, 'gstPercent', medicine.gstPercent.toString());
        updateItem(itemId, 'hsnCode', medicine.hsnCode);

        setMedicineSearches(prev => ({ ...prev, [itemId]: medicine.name }));
        setShowMedicineDropdown(prev => ({ ...prev, [itemId]: false }));
    };

    // Filter medicines based on search
    const getFilteredMedicines = (itemId: number) => {
        const searchTerm = medicineSearches[itemId] || '';
        return medicines.filter(medicine =>
            medicine.name.toLowerCase().includes(searchTerm.toLowerCase())
        );
    };

    // Calculate totals
    useEffect(() => {
        const grossTotal = purchaseBill.items.reduce((sum, item) => {
            const qty = parseFloat(item.qty) || 0;
            const rate = parseFloat(item.purchaseRate) || 0;
            return sum + (qty * rate);
        }, 0);

        const gstTotal = purchaseBill.items.reduce((sum, item) => {
            const qty = parseFloat(item.qty) || 0;
            const rate = parseFloat(item.purchaseRate) || 0;
            const gstPercent = parseFloat(item.gstPercent) || 0;
            return sum + ((qty * rate * gstPercent) / 100);
        }, 0);

        const cgst = gstTotal / 2;
        const sgst = gstTotal / 2;
        const igst = 0; // For inter-state transactions

        const subtotal = grossTotal + gstTotal - purchaseBill.discount + purchaseBill.cessAmount;
        const roundOff = Math.round(subtotal) - subtotal;
        const finalTotal = subtotal + roundOff;

        setPurchaseBill(prev => ({
            ...prev,
            grossTotal,
            gstTotal,
            cgst,
            sgst,
            igst,
            roundOff,
            finalTotal
        }));
    }, [purchaseBill.items, purchaseBill.discount, purchaseBill.cessAmount]);

    // Save purchase bill
    const savePurchaseBill = async () => {
        try {
            const purchaseData = {
                ...purchaseBill,
                items: purchaseBill.items
            };

            const response = await fetch('https://localhost:7112/api/PurchaseBills', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(purchaseData)
            });

            if (response.ok) {
                alert('Purchase bill saved successfully!');
            } else {
                alert('Error saving purchase bill');
            }
        } catch (err) {
            console.error('Failed to save purchase bill:', err);
            alert('Error saving purchase bill');
        }
    };

    // Delete purchase bill
    const deletePurchaseBill = () => {
        if (window.confirm('Are you sure you want to delete this purchase bill?')) {
            setPurchaseBill({
                purchaseId: purchaseBill.purchaseId + 1,
                date: new Date().toISOString().split('T')[0],
                supplierId: 0,
                supplierName: '',
                supplierAddress: '',
                gstIn: '',
                invoiceNo: '',
                invoiceDate: new Date().toISOString().split('T')[0],
                paymentType: 'Credit',
                items: [],
                grossTotal: 0,
                gstTotal: 0,
                roundOff: 0,
                discount: 0,
                cgst: 0,
                sgst: 0,
                igst: 0,
                cessAmount: 0,
                finalTotal: 0
            });
        }
    };

    return (
         <div className="min-h-screen bg-gray-100 p-4">
            

            {/* Main Content */}
             <div className="max-w-7xl mx-auto">
                {/* Purchase Bill Form */}
               <div className="bg-white rounded-lg shadow-lg mb-6 overflow-hidden">
                    <div className="max-w-full mx-auto bg-white shadow-lg rounded-lg overflow-hidden">
                        {/* Header Section */}
                        <div className="bg-slate-600 text-white px-4 py-3 flex justify-between items-center">
                            <div className="flex items-center space-x-4">
                                <ArrowLeft className="h-5 w-5" />
                                <h1 className="text-xl font-bold">Purchase Invoice</h1>
                            </div>
                            <div className="flex space-x-2">
                                <button
                                    onClick={savePurchaseBill}
                                    className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-md text-sm"
                                >
                                    Save
                                </button>
                                <button
                                    onClick={deletePurchaseBill}
                                    className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded-md text-sm"
                                >
                                    Delete
                                </button>
                            </div>
                        </div>

                        {/* Form Fields */}
                        <div className="p-4 bg-gray-50 border-b">
                            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                                {/* Left Column */}
                                <div className="space-y-4">
                                    <div className="grid grid-cols-2 gap-4">
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700 mb-1">Pur ID</label>
                                            <input
                                                type="text"
                                                value={purchaseBill.purchaseId}
                                                readOnly
                                                className="w-full px-3 py-2 border border-gray-300 rounded-md bg-gray-100 text-sm"
                                            />
                                        </div>
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700 mb-1">Date</label>
                                            <input
                                                type="date"
                                                value={purchaseBill.date}
                                                onChange={(e) => setPurchaseBill(prev => ({ ...prev, date: e.target.value }))}
                                                className="w-full px-3 py-2 border border-gray-300 rounded-md text-sm"
                                            />
                                        </div>
                                    </div>

                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-1">Supplier ID</label>
                                        <div className="flex">
                                            <select
                                                value={purchaseBill.supplierId}
                                                onChange={(e) => setPurchaseBill(prev => ({ ...prev, supplierId: Number(e.target.value) }))}
                                                className="flex-1 px-3 py-2 border border-gray-300 rounded-l-md text-sm"
                                            >
                                                <option value={0}>Select Supplier</option>
                                                {suppliers.map(supplier => (
                                                    <option key={supplier.id} value={supplier.id}>
                                                        {supplier.id} - {supplier.name}
                                                    </option>
                                                ))}
                                            </select>
                                            <button
                                                onClick={fetchSupplierDetails}
                                                disabled={!purchaseBill.supplierId || loading}
                                                className="bg-slate-600 hover:bg-slate-700 disabled:bg-gray-400 text-white px-4 py-2 rounded-r-md text-sm"
                                            >
                                                GET
                                            </button>
                                        </div>
                                    </div>

                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-1">Name</label>
                                        <input
                                            type="text"
                                            value={purchaseBill.supplierName}
                                            readOnly
                                            className="w-full px-3 py-2 border border-gray-300 rounded-md bg-gray-100 text-sm"
                                        />
                                    </div>

                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-1">Address</label>
                                        <textarea
                                            value={purchaseBill.supplierAddress}
                                            readOnly
                                            rows={3}
                                            className="w-full px-3 py-2 border border-gray-300 rounded-md bg-gray-100 resize-none text-sm"
                                        />
                                    </div>
                                </div>

                                {/* Right Column */}
                                <div className="space-y-4">
                                    <div className="grid grid-cols-2 gap-4">
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700 mb-1">Inv No</label>
                                            <input
                                                type="text"
                                                value={purchaseBill.invoiceNo}
                                                onChange={(e) => setPurchaseBill(prev => ({ ...prev, invoiceNo: e.target.value }))}
                                                className="w-full px-3 py-2 border border-gray-300 rounded-md text-sm"
                                            />
                                        </div>
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700 mb-1">Inv Date</label>
                                            <input
                                                type="date"
                                                value={purchaseBill.invoiceDate}
                                                onChange={(e) => setPurchaseBill(prev => ({ ...prev, invoiceDate: e.target.value }))}
                                                className="w-full px-3 py-2 border border-gray-300 rounded-md text-sm"
                                            />
                                        </div>
                                    </div>

                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-1">GST IN</label>
                                        <input
                                            type="text"
                                            value={purchaseBill.gstIn}
                                            readOnly
                                            className="w-full px-3 py-2 border border-gray-300 rounded-md bg-gray-100 text-sm"
                                        />
                                    </div>

                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-1">Payment</label>
                                        <select
                                            value={purchaseBill.paymentType}
                                            onChange={(e) => setPurchaseBill(prev => ({ ...prev, paymentType: e.target.value }))}
                                            className="w-full px-3 py-2 border border-gray-300 rounded-md text-sm"
                                        >
                                            <option value="Credit">Credit</option>
                                            <option value="Cash">Cash</option>
                                            <option value="Other">Other</option>
                                        </select>
                                    </div>

                                    <div className="pt-4">
                                        <button
                                            onClick={addItem}
                                            className="bg-slate-600 hover:bg-slate-700 text-white px-4 py-2 rounded-md flex items-center space-x-2 text-sm"
                                        >
                                            <Plus className="h-4 w-4" />
                                            <span>Add Item</span>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>

                        {/* Items Table */}
                        <div className="overflow-x-auto">
                            <table className="w-full">
                                <thead>
                                    <tr className="bg-cyan-500 text-white">
                                        <th className="px-2 py-3 text-left text-xs font-medium border-r border-cyan-600">Item Code</th>
                                        <th className="px-2 py-3 text-left text-xs font-medium border-r border-cyan-600">Medicine Name</th>
                                        <th className="px-2 py-3 text-left text-xs font-medium border-r border-cyan-600">Batch</th>
                                        <th className="px-2 py-3 text-left text-xs font-medium border-r border-cyan-600">Expiry</th>
                                        <th className="px-2 py-3 text-center text-xs font-medium border-r border-cyan-600">Qty</th>
                                        <th className="px-2 py-3 text-center text-xs font-medium border-r border-cyan-600">Free</th>
                                        <th className="px-2 py-3 text-center text-xs font-medium border-r border-cyan-600">P.Rate</th>
                                        <th className="px-2 py-3 text-center text-xs font-medium border-r border-cyan-600">S.Rate</th>
                                        <th className="px-2 py-3 text-center text-xs font-medium border-r border-cyan-600">GST</th>
                                        <th className="px-2 py-3 text-center text-xs font-medium border-r border-cyan-600">Amount</th>
                                        <th className="px-2 py-3 text-center text-xs font-medium border-r border-cyan-600">HSN</th>
                                        <th className="px-2 py-3 text-center text-xs font-medium">Action</th>
                                    </tr>
                                </thead>
                                <tbody className="bg-white">
                                    {purchaseBill.items.map((item, index) => (
                                        <tr key={item.id} className="hover:bg-gray-50 border-b">
                                            <td className="px-2 py-2 border-r border-gray-200">
                                                <input
                                                    type="text"
                                                    value={item.itemCode}
                                                    onChange={(e) => updateItem(item.id, 'itemCode', e.target.value)}
                                                    className="w-full px-2 py-1 text-xs border border-gray-300 rounded"
                                                />
                                            </td>
                                            <td className="px-2 py-2 border-r border-gray-200 relative">
                                                <input
                                                    type="text"
                                                    value={medicineSearches[item.id] || item.medicineName}
                                                    onChange={(e) => handleMedicineSearch(item.id, e.target.value)}
                                                    onFocus={() => setShowMedicineDropdown(prev => ({ ...prev, [item.id]: true }))}
                                                    className="w-full px-2 py-1 text-xs border border-gray-300 rounded"
                                                    placeholder="Search medicine..."
                                                />
                                                {showMedicineDropdown[item.id] && (
                                                    <div className="absolute z-10 w-full bg-white border border-gray-300 rounded shadow-lg max-h-32 overflow-y-auto">
                                                        {getFilteredMedicines(item.id).map(medicine => (
                                                            <div
                                                                key={medicine.id}
                                                                onClick={() => selectMedicine(item.id, medicine)}
                                                                className="px-2 py-1 text-xs hover:bg-gray-100 cursor-pointer"
                                                            >
                                                                {medicine.name}
                                                            </div>
                                                        ))}
                                                    </div>
                                                )}
                                            </td>
                                            <td className="px-2 py-2 border-r border-gray-200">
                                                <input
                                                    type="text"
                                                    value={item.batchNo}
                                                    onChange={(e) => updateItem(item.id, 'batchNo', e.target.value)}
                                                    className="w-full px-2 py-1 text-xs border border-gray-300 rounded"
                                                />
                                            </td>
                                            <td className="px-2 py-2 border-r border-gray-200">
                                                <input
                                                    type="date"
                                                    value={item.expiryDate}
                                                    onChange={(e) => updateItem(item.id, 'expiryDate', e.target.value)}
                                                    className="w-full px-2 py-1 text-xs border border-gray-300 rounded"
                                                />
                                            </td>
                                            <td className="px-2 py-2 border-r border-gray-200">
                                                <input
                                                    type="text"
                                                    value={item.qty}
                                                    onChange={(e) => updateItem(item.id, 'qty', e.target.value)}
                                                    className="w-full px-2 py-1 text-xs border border-gray-300 rounded text-center"
                                                />
                                            </td>
                                            <td className="px-2 py-2 border-r border-gray-200">
                                                <input
                                                    type="text"
                                                    value={item.freeQty}
                                                    onChange={(e) => updateItem(item.id, 'freeQty', e.target.value)}
                                                    className="w-full px-2 py-1 text-xs border border-gray-300 rounded text-center"
                                                />
                                            </td>
                                            <td className="px-2 py-2 border-r border-gray-200">
                                                <input
                                                    type="text"
                                                    value={item.purchaseRate}
                                                    onChange={(e) => updateItem(item.id, 'purchaseRate', e.target.value)}
                                                    className="w-full px-2 py-1 text-xs border border-gray-300 rounded text-center"
                                                />
                                            </td>
                                            <td className="px-2 py-2 border-r border-gray-200">
                                                <input
                                                    type="text"
                                                    value={item.sellingRate}
                                                    onChange={(e) => updateItem(item.id, 'sellingRate', e.target.value)}
                                                    className="w-full px-2 py-1 text-xs border border-gray-300 rounded text-center"
                                                />
                                            </td>
                                            <td className="px-2 py-2 border-r border-gray-200">
                                                <input
                                                    type="text"
                                                    value={item.gstPercent}
                                                    onChange={(e) => updateItem(item.id, 'gstPercent', e.target.value)}
                                                    className="w-full px-2 py-1 text-xs border border-gray-300 rounded text-center"
                                                />
                                            </td>
                                            <td className="px-2 py-2 border-r border-gray-200 text-center text-xs font-medium">
                                                {item.amount.toFixed(2)}
                                            </td>
                                            <td className="px-2 py-2 border-r border-gray-200">
                                                <input
                                                    type="text"
                                                    value={item.hsnCode}
                                                    onChange={(e) => updateItem(item.id, 'hsnCode', e.target.value)}
                                                    className="w-full px-2 py-1 text-xs border border-gray-300 rounded text-center"
                                                />
                                            </td>
                                            <td className="px-2 py-2 text-center">
                                                <button
                                                    onClick={() => removeItem(item.id)}
                                                    className="text-red-600 hover:text-red-800 hover:bg-red-50 p-1 rounded"
                                                >
                                                    <Trash2 className="h-4 w-4" />
                                                </button>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>

                        {/* Summary Section */}
                        <div className="bg-slate-600 text-white p-4">
                            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                                {/* Tax Details */}
                                <div className="space-y-3">
                                    <div className="grid grid-cols-2 gap-4">
                                        <div>
                                            <label className="block text-xs font-medium mb-1">Gross Total</label>
                                            <div className="px-3 py-2 bg-white text-black rounded text-right text-sm">
                                                {purchaseBill.grossTotal.toFixed(2)}
                                            </div>
                                        </div>
                                        <div>
                                            <label className="block text-xs font-medium mb-1">CGST</label>
                                            <div className="px-3 py-2 bg-white text-black rounded text-right text-sm">
                                                {purchaseBill.cgst.toFixed(2)}
                                            </div>
                                        </div></div>
                                    <div className="grid grid-cols-2 gap-4">
                                        <div>
                                            <label className="block text-xs font-medium mb-1">SGST</label>
                                            <div className="px-3 py-2 bg-white text-black rounded text-right text-sm">
                                                {purchaseBill.sgst.toFixed(2)}
                                            </div>
                                        </div>
                                        <div>
                                            <label className="block text-xs font-medium mb-1">IGST</label>
                                            <div className="px-3 py-2 bg-white text-black rounded text-right text-sm">
                                                {purchaseBill.igst.toFixed(2)}
                                            </div>
                                        </div>
                                    </div>
                                    <div className="grid grid-cols-2 gap-4">
                                        <div>
                                            <label className="block text-xs font-medium mb-1">GST Total</label>
                                            <div className="px-3 py-2 bg-white text-black rounded text-right text-sm">
                                                {purchaseBill.gstTotal.toFixed(2)}
                                            </div>
                                        </div>
                                        <div>
                                            <label className="block text-xs font-medium mb-1">Round Off</label>
                                            <div className="px-3 py-2 bg-white text-black rounded text-right text-sm">
                                                {purchaseBill.roundOff.toFixed(2)}
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                {/* Additional Fields */}
                                <div className="space-y-3">
                                    <div>
                                        <label className="block text-xs font-medium mb-1">Discount</label>
                                        <input
                                            type="number"
                                            value={purchaseBill.discount}
                                            onChange={(e) => setPurchaseBill(prev => ({ ...prev, discount: parseFloat(e.target.value) || 0 }))}
                                            className="w-full px-3 py-2 border border-gray-300 rounded text-right text-sm"
                                            step="0.01"
                                        />
                                    </div>
                                    <div>
                                        <label className="block text-xs font-medium mb-1">Cess Amount</label>
                                        <input
                                            type="number"
                                            value={purchaseBill.cessAmount}
                                            onChange={(e) => setPurchaseBill(prev => ({ ...prev, cessAmount: parseFloat(e.target.value) || 0 }))}
                                            className="w-full px-3 py-2 border border-gray-300 rounded text-right text-sm"
                                            step="0.01"
                                        />
                                    </div>
                                </div>

                                {/* Final Total */}
                                <div className="space-y-3">
                                    <div className="text-right">
                                        <label className="block text-lg font-bold mb-2">Total</label>
                                        <div className="text-3xl font-bold text-red-400">
                                            â‚¹{purchaseBill.finalTotal.toFixed(2)}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Mobile overlay */}
            {sidebarOpen && (
                <div
                    className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
                    onClick={() => setSidebarOpen(false)}
                />
            )}
        </div>
    );
};

export default PurchaseBillPage;